
import { defineComponent, inject } from "vue";

export default defineComponent({
  name: "Mypage",
  setup() {
    /* ログインの有無 */
    const isLoggedIn: any = inject("isLoggedIn");
    // ログインしたユーザの情報
    const loginData: any = inject("loginData");

    return {
      // データ
      isLoggedIn,
      loginData,
      // 関数
    };
  },
});
